<template>
    <div class="forget">
        <header>
            <router-link to="/info">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <span>重置密码</span>
        </header>
        <section>
            <div><input type="text" placeholder="账号" v-model="username"></div>
            <div><input type="text" placeholder="旧密码" v-model="oldpassWord"></div>
            <div><input type="text" placeholder="请输入新密码" v-model="newpassword"></div>
            <div><input type="text" placeholder="请确认密码" v-model="confirmpassword"></div>
            <div class="input_c">
                <input type="text" placeholder="验证码" v-model="captcha_code">
                <div>
                    <img :src="capUrl">
                    <div>
                        <p>看不清</p>
                        <p @click="changeCode">换一张</p>
                    </div>
                </div>
            </div>
        </section>
        <div class="change" @click="changepassword">确认修改</div>
        <div class="yinying" v-if="isShow"></div>
        <div class="changeS" v-if="isShow">
            <p><i class="el-icon-warning-outline"></i></p>
            <p>{{p}}</p>
            <div @click="isShow = !isShow">确认</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Forget",
        data(){
            return{
                username:"",
                oldpassWord:"",
                newpassword:"",
                confirmpassword:"",
                captcha_code:"",
                capUrl:"",
                p:"",
                isShow:false,
            }
        },
        created(){
            this.requestCode();
        },
        methods:{
            changepassword(){
                this.axios.post("/v2/changepassword",{
                    username:this.username,
                    oldpassWord:this.oldpassWord,
                    newpassword:this.newpassword,
                    confirmpassword:this.confirmpassword,
                    captcha_code:this.captcha_code,
                }).then(res=>{
                    console.log(res);
                    if(res.success){
                        this.p = res.success;
                    }else {
                        this.p = res.message;
                    }
                    this.isShow = !this.isShow;
                }).catch(err=>{});
            },
            changeCode(){
                this.requestCode();
            },
            //请求验证码的方法
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.capUrl = res.code;
                }).catch(err=>{});
            }
        }
    }
</script>

<style scoped>
    .forget{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    section{
        background-color: #fff;
        margin-top: .6rem;
        display: block;
    }
    section>div{
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    section>div:nth-child(1){
        padding: .3rem .8rem;
    }
    section>div>input{
        font-size: .7rem;
        color: #666;
        text-decoration: none;
        border: none;
        outline: none;
    }
    .input_c>div{
        display: flex;
        align-items: center;
    }
    .input_c>div p{
        font-size: .55rem;
        color:#666;
    }
    .input_c>div p+p{
        color:#3b95e9;
        margin-top: .2rem;
    }
    .change{
        margin: 1rem .5rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }
    .yinying{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 15;
        background-color: #000;
        opacity: 0.3;
    }
    .changeS{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
        z-index: 15;
    }
    .changeS>p:nth-child(1){
        font-size: 4rem;
        color: #f8cb86;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .changeS>p:nth-child(2){
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .changeS div{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>